import Taro from '@tarojs/taro-h5';
import { View } from "@tarojs/components";
import { Component, eventCenter } from "@tarojs/taro-h5";
import Nerv from "nervjs";
import * as utils from '../../libs/utils';
class ALink extends Component {
  constructor() {
    super(...arguments);
  }
  render() {
    const props = this.props;
    const cprops = {
      ...props,
      style: {
        cursor: "pointer"
      }
    };
    delete cprops.to;
    const className = cprops.className;
    return <View className=" link-class" style={cprops.style} onClick={this.goTo.bind(this, props.to)}>
        {this.props.children}
      </View>;
  }
  goTo = ({ url, params }) => {
    eventCenter.trigger('beforeNavigate');
    Taro.navigateTo({
      url: url + (params ? "?" + utils.param(params) : "")
    });
    return false;
  };
}
ALink.defaultProps = {
  to: {
    url: "",
    params: ""
  },
  className: "",
  klassName: ""
};
ALink.externalClasses = ["link-class"];
ALink.options = {
  addGlobalClass: true
};
export default ALink;