import * as tslib_1 from "tslib";
import { Component } from "@tarojs/taro-h5";
import Nerv from "nervjs";
import { View, Image, Text } from "@tarojs/components";
import ALink from "../../components/link";
import { connect } from "@tarojs/redux-h5";
import * as actions from "../../actions/auth";
import './index.scss';
let UserInfo = class UserInfo extends Component {
  componentWillReceiveProps(nextProps) {
    // console.log(this.props, nextProps);
  }
  componentWillMount() {
    this.props.authCheckState();
  }
  render() {
    const userInfo = this.props.userInfo;
    return <View className="user-info">
      {!userInfo.loginname ? <ALink link-class="login-no" to={{ url: "/pages/login/index" }}>
            <View className="login">
              <View>登录</View>
            </View>
      </ALink> : <ALink link-class="login-yes" to={{ url: "/pages/user/index", params: { loginname: userInfo.loginname } }}>
            <View className="avertar">
              {userInfo.avatar_url ? <Image class="avertar" src={userInfo.avatar_url} /> : ""}
            </View>
            <View className="info">
              {userInfo.loginname ? <Text>{userInfo.loginname}</Text> : ""}
            </View>
          </ALink>}
      </View>;
  }
};
UserInfo = tslib_1.__decorate([connect(({ auth }) => ({
  userInfo: auth
}), dispatch => ({
  authCheckState() {
    dispatch(actions.authCheckState());
  }
}))], UserInfo);
export default UserInfo;